<template>
	<div id="vip-redpacket-month">
		<public-header title="会员红包" v-if="!isWeixin"></public-header>
		<div class="content">
			<div class="flex">
				<span class="status icon icon-roundcheckfill"></span>
				<span>使用中</span>
			</div>
			<div class="card">
				<div class="font-18 font-bold">{{vipRedpacket.params.title}}</div>
				<div class="font-12 margin-10-t">{{member.starttime_day}}-{{member.endtime_day}}</div>
			</div>
			<div class="redpackets">
				<div class="flex margin-5-l">
					<div class="font-bold">可用红包：{{member.redpacket_num_avaliable}}张</div>
					<div class="c-gray font-12 margin-10-l">共{{redpackets.length}}张</div>
				</div>
				<div class="list">
					<div class="redpacket-item" :class="{used: item.status != 1}" v-for="(item, index) in redpackets" :key="index">
						<div class="inner">

							<div class="title" v-if="item.sid > 0">
								<span class="logo">
									<img class="img-100" :src="item.logo" alt="">
								</span>
								<div class="store-title ellipsis">{{item.title}}</div>
							</div>
							<div class="title" v-else>
								<span>{{item.title}}</span>
							</div>
							<div class="discount">
								<span class="c-danger">
									{{Lang.dollarSign}}<span class="font-20">{{item.discount}}</span>
								</span>
								<span class="margin-5-l">无门槛</span>
							</div>
							<router-link tag="div" :to="util.getUrl({path: '/pages/home/index'})" class="use" v-if="item.status == 1">
								<span>去使用</span>
								<i class="icon icon-right font-12"></i>
							</router-link>
							<div class="use" v-else>
								<div class="have-used"></div>
							</div>
						</div>
					</div>
				</div>
				<div class="flex-center hide">
					<span class="margin-5-r">展开全部</span>
					<i class="icon icon-unfold font-12 c-gray"></i>
				</div>
			</div>
			<router-link :to="util.getUrl({path: 'pages/member/redPacket/index', query: {status: 2}})" tag="div" class="flex-center padding-15-tb">
				<span class="margin-5-r">查看无效券</span>
				<i class="icon icon-right font-12 c-gray"></i>
			</router-link>
		</div>
		<iloading v-if="preLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				vipRedpacket: {
					params: {}
				},
				member: {},
				redpackets: [],
				preLoading: true
			}
		},
		components: {
			PublicHeader
		},
		methods: {
			onLoad() {
				var that = this;
				that.util.request({
					url: 'vipRedpacket/index/redpacket',
				}).then((res) => {
					that.preLoading = false;
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message, that.util.getUrl({path: '/plugin/pages/vipRedpacket/index'}), 1500, 'replace');
						return;
					}
					result = result.message;
					that.member = that.util.extend(that.member, result.member);
					that.vipRedpacket = that.util.extend(that.vipRedpacket, result.vipRedpacket);
					that.redpackets = [...result.redpackets];
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#vip-redpacket-month #public-header .van-nav-bar{
		background-color: #353130!important;
	}
	#vip-redpacket-month .content{
		padding: 15px 10px;
		font-size: 14px;
	}
	#vip-redpacket-month .status{
		position: relative;
		font-size: 20px;
		color: #63C068;
		margin-right: 10px;
	}
	#vip-redpacket-month .status:after{
		content: '';
		position: absolute;
		bottom: -15px;
		left: 50%;
		transform: translateX(-50%);
		height: 15px;
		border-left: 3px solid #DEDEDE;
	}
	#vip-redpacket-month .card{
		margin-top: 15px;
		padding: 15px;
		background-image: linear-gradient(90deg, #F7E2CD, #E6B993);
		border-radius: 5px 5px 0 0;
		color: #553B22
	}
	#vip-redpacket-month .redpackets{
		padding: 15px 10px;
		border-radius: 0 0 5px 5px;
		background-color: #fff;
	}
	#vip-redpacket-month .redpackets .list{
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		padding: 10px 0;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item{
		width: 50%;
		padding: 5px;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .inner{
		position: relative;
		overflow: hidden;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .inner:before, .redpackets .list .redpacket-item .inner:after{
		content: '';
		position: absolute;
		z-index: 1;
		left: -5px;
		bottom: 25px;
		width: 10px;
		height: 10px;
		border-radius: 50%;
		border: 1px solid #E1E1E1;
		box-sizing: border-box;
		background-color: #fff;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .inner:after{
		right: -5px;
		left: auto;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .title{
		width: 100%;
		height: 32px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		background-color: #F7F7F7;
		font-size: 12px;
		padding: 0 10px;
		text-align: center;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		border: 1px solid #E1E1E1;
		border-bottom: 0px;
		border-radius: 5px 5px 0 0;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item.used .title, .redpackets .list .redpacket-item.used .c-danger{
		color: #989794!important;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .title .label{
		display: inline-block;
		padding: 2px 4px;
		background-color: #FF2D4B;
		color: #fff;
		border-radius: 3px;
		margin-right: 5px;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item.used .title .label{
		background-color: #989794;
		color: #FEFEFE;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .title .logo{
		display: inline-block;
		width: 20px;
		height: 20px;
		font-size: 0px;
		border-radius: 2px;
		overflow: hidden;
		margin-right: 5px;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .title .store-title{
		max-width: 80%;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .discount{
		padding: 10px 0;
		font-size: 12px;
		text-align: center;
		border-left: 1px solid #E1E1E1;
		border-right: 1px solid #E1E1E1;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .use{
		position: relative;
		height: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 12px;
		color: #ff2d4b;
		border: 1px solid #E1E1E1;
		border-top: 0px;
		border-radius: 0 0 5px 5px;
	}
	#vip-redpacket-month .redpackets .list .redpacket-item .use:before{
		content: '';
		position: absolute;
		z-index: 1;
		left: 10px;
		right: 10px;
		top: 0px;
		border-top: 1px dashed #E1E1E1;
	}
	.redpackets .list .redpacket-item .use .have-used{
		display: block;
		position: absolute;
		width: 36px;
		height: 36px;
		bottom: 5px;
		left: 50%;
		transform: translateX(-50%);
		background: url(../../../static/img/wu.png);
		background-size: 100%;
		z-index: 100;
	}
</style>


